"use server";

import { auth } from "@/auth";
import clientPromise from "@/lib/mongodb";
import { GoogleGenerativeAI } from "@google/generative-ai";

const delay = (ms: number) => new Promise((res) => setTimeout(res, ms));

export async function getAiResponse({
  message,
  lifeStage,
  scenarioId,
  scenarioContext,
  isPractice,
  imageBase64
}: ChatParams) {
  const session = await auth();
  if (!session?.user) throw new Error("User must be authenticated.");

  const apiKey = process.env.GOOGLE_GENERATIVE_AI_API_KEY;
  if (!apiKey) {
    return {
      reflection: "The mirror is currently disconnected.",
      reassurance: "The system cannot find its credentials.",
      suggestion: "Check your .env.local file and restart the server."
    };
  }

  const genAI = new GoogleGenerativeAI(apiKey);
  // Using gemini-1.5-flash for faster, more stable performance to avoid timeouts
  const model = genAI.getGenerativeModel({ model: "gemini-1.5-flash" });

  try {
    const systemPrompt = isPractice 
      ? `You are a Social Coach. Scenario: ${scenarioId}. Context: ${scenarioContext}. Focus on prosody and reciprocal communication.`
      : `You are the 'Social Mirror'. Goal: Explicitly decode unspoken subtext, sarcasm, and hidden intent for someone with ASD Level 1.`;

    const parts: any[] = [
      { text: `System Instruction: ${systemPrompt}` },
      { text: `User Context: Life Stage is ${lifeStage}.` },
      { text: `User Message: ${message || "Analyze this artifact."}` },
      { text: `IMPORTANT: Respond ONLY as a raw JSON object with keys: "reflection", "reassurance", and "suggestion".` }
    ];

    if (imageBase64 && imageBase64.includes(",")) {
      const base64Data = imageBase64.split(",")[1];
      parts.push({ inlineData: { data: base64Data, mimeType: "image/png" } });
    }

    let result;
    let retries = 0;
    const maxRetries = 2;

    while (retries <= maxRetries) {
      try {
        result = await model.generateContent({
          contents: [{ role: "user", parts }],
          generationConfig: { 
            responseMimeType: "application/json", 
            temperature: 0.7 
          },
        });
        break; 
      } catch (e: any) {
        if (e.status === 429 && retries < maxRetries) {
          retries++;
          await delay(2000 * retries);
          continue;
        }
        throw e;
      }
    }

    // 1. EXTRACT AND VALIDATE TEXT
    const text = result?.response?.text();
    if (!text || text.trim() === "") {
      throw new Error("EMPTY_RESPONSE");
    }

    // 2. CLEAN AND PARSE WITH SAFETY
    const cleanedText = text.replace(/```json|```/g, "").trim();
    let data;
    try {
      data = JSON.parse(cleanedText);
    } catch (parseError) {
      console.error("JSON Parse Error:", parseError, "Raw text:", text);
      throw new Error("MALFORMED_JSON");
    }

    // 3. PERSISTENCE
    const client = await clientPromise;
    const db = client.db("mirrorDB"); 
    await db.collection("chat_history").insertOne({
      userId: session.user.id,
      lifeStage,
      scenarioId,
      isPractice,
      userMessage: message,
      hasImage: !!imageBase64,
      aiResponse: data,
      timestamp: new Date(),
    });

    return data;

  } catch (error: any) {
    console.error("Mirror Engine Error:", error);
    
    // Custom ASD-friendly Fallbacks
    if (error.message === "EMPTY_RESPONSE" || error.message === "MALFORMED_JSON") {
      return {
        reflection: "The mirror is currently silent or distorted.",
        reassurance: "Sometimes technical 'static' occurs during complex reflections.",
        suggestion: "Please try sending your message again or rephrasing it."
      };
    }

    return {
      reflection: "The mirror is momentarily hazy.",
      reassurance: "Social interactions take practice, and so does technology.",
      suggestion: "Try rephrasing or refreshing the page."
    };
  }
}